html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}
body.fijar-body {
  overflow: hidden;
}
body.scroll-fijo {
  padding-top: 10.6rem;
}

p {
  color: #0b0a0a;
  font-size: 2rem;
  line-height: 1.5;
}

.contenedor {
  width: 95;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
  font-family: "Montserrat", sans-serif;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  display: block;
}

.header {
  background-color: #0b0a0a;
}
.header h1 {
  color: #f8f8f8;
  text-align: center;
  margin: 1rem;
}
.header .contenido-header {
  padding: 2rem;
}
@media (min-width: 768px) {
  .header .contenido-header {
    display: flex;
    justify-content: space-between;
    text-align: center;
  }
}
@media (min-width: 768px) {
  .header.fijo {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    box-shadow: 0.3rem 0.3rem 0.3rem #d8c0cf;
  }
}

.navegacion-principal {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2rem;
}
.navegacion-principal a {
  margin-top: 1rem;
  color: #f8f8f8;
  font-size: 2.4rem;
}
.navegacion-principal a:hover {
  color: #807677;
}
@media (min-width: 768px) {
  .navegacion-principal a {
    margin-top: 0;
  }
}

.video {
  position: relative;
  max-height: 70rem;
  min-height: 50rem;
  overflow: hidden;
}
.video .overlay {
  position: absolute;
  height: 100%;
  width: 100%;
  background: rgb(0, 0, 0);
  background: radial-gradient(circle, rgba(0, 0, 0, 0.1517857143) 0%, rgba(0, 0, 0, 0.7848389356) 100%);
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.video video {
  width: auto;
  display: block;
}
@media (min-width: 996px) {
  .video video {
    width: 100%;
  }
}

.contenido-video {
  z-index: 1;
  margin: 0 1.5rem;
}
.contenido-video h2 {
  color: #f8f8f8;
  font-size: 7rem;
  margin-bottom: 2rem;
}
.contenido-video p {
  display: inline;
  background-color: #807677;
  color: #f8f8f8;
  padding: 0.9rem;
  font-weight: 700;
  font-size: 2.4rem;
}
@media (min-width: 768px) {
  .contenido-video p {
    padding: 0.8rem 2rem;
  }
}

@media (min-width: 768px) {
  .contenido-logo {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .contenido-logo img {
    width: 20rem;
    height: 20rem;
    margin-left: 2rem;
    margin-right: 2rem;
    margin-top: 2rem;
  }
}

.sobre-luna {
  padding: 5rem 1rem;
}
@media (min-width: 768px) {
  .sobre-luna {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.imagen-sobre-nosotros img {
  max-width: 100%;
  display: block;
}

.contenido-sobre-nosotros {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .contenido-sobre-nosotros {
    margin-top: 0rem;
  }
}
.contenido-sobre-nosotros h2 {
  margin-bottom: 2rem;
  font-size: 4rem;
}
.contenido-sobre-nosotros .ubicacion {
  color: #c2114b;
  font-weight: 700;
}

.opcion-menu {
  background-color: #100f0b;
  padding: 5rem 0;
}
.opcion-menu h3, .opcion-menu p {
  color: #f8f8f8;
  text-align: center;
}
.opcion-menu .tipo-alimento {
  text-transform: uppercase;
  margin-bottom: 5rem;
  margin-top: 5rem;
}
.opcion-menu .opciones-alimento-unico {
  color: #0b0a0a;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 1.5rem;
}

@media (min-width: 768px) {
  .contenedor-menu {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.menu {
  padding-top: 2rem;
  margin: 1.5rem;
}
.menu p:after {
  content: "";
  display: block;
  width: 7rem;
  height: 7rem;
  background-size: 5rem;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 1rem auto 0 auto;
}
.menu .nombre-alimento {
  font-weight: 700;
  color: #0b0a0a;
}

.bebida .nombre-alimento:after {
  background-image: url("../../build/img/menu/cup.png");
}

.cafe .nombre-alimento:after {
  background-image: url("../../build/img/menu/coffee.png");
}

.comida .nombre-alimento:after {
  background-image: url("../../build/img/menu/salad.png");
}

.postre .nombre-alimento:after {
  background-image: url("../../build/img/menu/candy.png");
}

.hamburguesa .nombre-alimento:after {
  background-image: url("../../build/img/menu/burger.png");
}

.bread .nombre-alimento:after {
  background-image: url("../../build/img/menu/bread.png");
}

.crepa .nombre-alimento:after {
  background-image: url("../../build/img/menu/brand-nuxt.png");
}

.opciones-alimento {
  margin: 0;
  padding: 0;
  list-style: none;
}
.opciones-alimento li {
  padding: 2.5rem;
  color: #0b0a0a;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
}

.contenedor-menu-largo .opciones-alimento {
  padding: 0;
  text-align: center;
}

.bg-crema {
  background-color: #e7dbd8;
}
.bg-crema li:nth-child(odd) {
  background-color: #d4beb8;
}

.bg-cafe-gris {
  background-color: #807677;
}
.bg-cafe-gris li:nth-child(odd) {
  background-color: #655e5e;
}

.galeria {
  padding: 5rem 1rem;
}
.galeria h3 {
  text-align: center;
}

.galeria-imagenes {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 480px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 768px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 996px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2rem;
  }
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.5);
}
.overlay picture {
  display: block;
  max-width: 60rem;
}

.btn-cerrar {
  color: #0b0a0a;
  background-color: #e7dbd8;
  width: 5rem;
  height: 5rem;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 700;
  font-size: 2.5rem;
}
.btn-cerrar:hover {
  cursor: pointer;
}

.maps {
  display: block;
  padding: 5rem 0;
  background-color: #100f0b;
}
.maps h3 {
  text-align: center;
  color: #f8f8f8;
}

.contenedor-maps {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 1rem;
}
@media (min-width: 996px) {
  .contenedor-maps {
    margin: 0 auto;
  }
}

#map {
  z-index: 0;
  width: 90rem;
  height: 50rem;
}

.footer-redes {
  background-color: #e7dbd8;
}

.contenedor-redes {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10rem;
}

.facebook, .instagram {
  display: flex;
  justify-content: center;
  align-items: center;
}
.facebook img, .instagram img {
  margin-right: 1rem;
  display: block;
  width: 5rem;
}
.facebook:hover, .instagram:hover {
  cursor: pointer;
}