@use "base/variables" as v;
@use "base/mixins" as m;

.galeria {
    padding: 5rem 1rem;

    h3 {
        text-align: center;
    }


}

.galeria-imagenes {
    @include m.resetearLista;
    @include m.grid(2, 2rem);

    @include m.telefono {
        @include m.grid(3, 2rem);
    }
    
    
    @include m.tablet {
        @include m.grid(4, 2rem);
    }

    @include m.desktop {
        @include m.grid(6, 2rem)
    }

}


.overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    width: 100%;
    height: 100%;
    
    display: grid;
    place-items: center;

    background-color: rgb(0 0 0 / .5);


    picture {
        display: block;
        max-width: 60rem;
    }
    
}

.btn-cerrar {
    color: v.$negro;
    background-color: v.$crema;
    width: 5rem;
    height: 5rem;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 700;
    font-size: 2.5rem;

    &:hover {
        cursor: pointer;
    }
}